/* ÂÖ®Â±ÄËÆäÊï∏ */
$primary-color: #8b5a2b;
$hover-color: #a07246;
$font-family: 'Comic Sans MS', cursive, sans-serif;
$button-padding: 1.3vh 2.6vh;
$button-font-size: 1.3vw;
$button-border-radius: 0.5vw;
$box-shadow: 0 0.4vw 0.8vw rgba(0, 0, 0, 0.2);

/* Âü∫Êú¨ÂÆπÂô®Ê®£Âºè */
.container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
    overflow: hidden;
    background: none;
    color: #fff;
    text-align: center;
    font-family: $font-family;
}

/* ÊåâÈàïÂÆπÂô®Ê®£Âºè */
.buttonContainer {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
}

/* ÊåâÈàïÂü∫Êú¨Ê®£Âºè */
.button {
    padding: $button-padding;
    font-size: $button-font-size;
    background-color: $primary-color;
    color: white;
    border: none;
    border-radius: $button-border-radius;
    cursor: pointer;
    box-shadow: $box-shadow;
    transition: background-color 0.3s ease, transform 0.3s ease;
    line-height: 1.5;
    text-align: center;

    &:hover {
        background-color: $hover-color;
        transform: scale(1.05);
    }
}

/* ÂÖ∑È´îÊåâÈàïÊ®£Âºè */
.foundButton,
.endButton {
    @extend .button;
}

/* Â™íÈ´îÊü•Ë©¢ÔºöÈÅ©ÊáâÂπ≥ÊùøË®≠ÂÇô */
@media (max-width: 768px) {
    .button {
        font-size: 2.6vw;
        padding: 2vw 4vw;
        border-radius: 1vw;
    }
}

/* Â™íÈ´îÊü•Ë©¢ÔºöÈÅ©ÊáâÊâãÊ©üË®≠ÂÇô */
@media (max-width: 480px) {
    .button {
        font-size: 3vw;
        padding: 2.5vw 5vw;
        border-radius: 1.5vw;
    }
}

/* Èö±ËóèËÉåÊôØÊ®£Âºè */
.hideBackground {
    background-image: none !important;
}